import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'

const SELECT_ELEMENT = 'SELECT_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js

const CANCEL_ELEMENT = 'CANCEL_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js
const DELETE_ELEMENT = 'DELETE_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js'
const EDIT_ELEMENT = 'EDIT_ELEMENT'

const SAVE_DETAIL = 'SAVE_DETAIL' // To : src/routes/Shop/routes/Editor/modules/preview.js
const PAGE_PUBLISH_START = 'PAGE_PUBLISH_START'
const PAGE_PUBLISH_END = 'PAGE_PUBLISH_END'

const UPDATE_STORE = 'UPDATE_STORE'

export const saveDetail = (element, detail) => dispatch => dispatch({
  type: SAVE_DETAIL,
  id: element.id,
  detail
})

const sleep = ms => new Promise(resolve => setTimeout(resolve, ms))
const getPublishStatus = async (deployId, platform) => {
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/deployPage/${deployId}/status`)
  let result = []
  if (platform !== 3) {
    result = await fetchUtil.getJSON(apiUrl)
    return result.status
  } else {
    result = await fetchUtil.getJSON(apiUrl)
    let arr = []
    arr[0] = result[0].status
    arr[1] = result[1].status
    return arr
  }
}

const repeatPublishStatus = async (deployId, platform) => {
  const result = await getPublishStatus(deployId, platform)
  if (platform !== 3) {
    if (result !== 2 && result !== 3) {
      await sleep(1000)
      return await repeatPublishStatus(deployId, platform)
    } else {
      return await result
    }
  } else {
    for (let i = 0; i < result.length; i++) {
      if (result[i] !== 2 && result[i] !== 3 || result[i + 1] !== 2 && result[i + 1] !== 3) {
        await sleep(1000)
        return await repeatPublishStatus(deployId, platform)
      } else {
        return await result
      }
    }
  }
}

export const publishPage = pageId => (dispatch, getState) => {
  const state = getState()
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/store/3/page/${pageId}/publish`)

  return fetchUtil.postJSON(apiUrl, { viewData: state.preview, pageType: 0 }, {
    formDataType: 'FormData'
  }).then(data => {
    dispatch(startPublishPage())
    const { platform, deployIdIos, deployIdAndroid } = data
    const deployId = platform === 1
       ? deployIdIos : platform === 2
       ? deployIdAndroid : deployIdAndroid + ',' + deployIdIos
    repeatPublishStatus(deployId, platform).then(pubResult => {
      dispatch(endPublishPage(pubResult))
    }).catch(e => {
      console.log(e)
    })
  })
}

export const savePage = pageId => (dispatch, getState) => {
  const state = getState()
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/store/3/page/${pageId}`)

  return fetchUtil.postJSON(apiUrl, { viewData: state.preview, pageType: 0 }, {
    formDataType: 'FormData'
  })
}

export const startPublishPage = () => ({
  type: PAGE_PUBLISH_START
})

export const endPublishPage = (deployStatus) => ({
  type: PAGE_PUBLISH_END,
  deployStatus
})

export const cancelElement = () => dispatch => dispatch({
  type: CANCEL_ELEMENT
})

export const deleteElement = id => dispatch => dispatch({
  type: DELETE_ELEMENT,
  id
})

export const editElement = (fieldID, filedValue) => dispatch => dispatch({
  type: EDIT_ELEMENT,
  fieldID,
  filedValue
})

export const updateStore = data => ({
  type: UPDATE_STORE,
  data
})

const ACTION_HANDLERS = {
  [UPDATE_STORE]: (state, action) => {
    return {
      ...state,
      ...action.data
    }
  },

  [CANCEL_ELEMENT]: state => ({ ...state, element: {} }),
  [DELETE_ELEMENT]: state => ({ ...state, element: {} }),
  [EDIT_ELEMENT]: (state, action) => {
    const { element } = state
    const { codeSetting } = element
    const { fieldID, filedValue } = action

    return {
      ...state,
      element: {
        ...element,
        codeSetting: codeSetting.map(item => {
          if (item.id === fieldID) {
            return {
              ...item,
              value: filedValue
            }
          }
          return item
        })
      }
    }
  },
  [SELECT_ELEMENT]: (state, action) => {
    return ({ ...state, element: action.selectedElement })
  },
  [PAGE_PUBLISH_START]: state => ({ ...state, pagePublish: 'start' }),
  [PAGE_PUBLISH_END]: (state, action) => {
    const deployStatus = Array.isArray(action.deployStatus) && action.deployStatus[0] === action.deployStatus[1]
    ? action.deployStatus[0] : action.deployStatus
    return ({
      ...state,
      pagePublish: deployStatus === 2 ? 'end' : 'failure'
    })
  }
}

export const actions = {
  cancelElement,
  saveDetail
}

const initState = {
  qrcode: '',
  element: {},
  pagePublish: 'end'
}

export default function detailReducer(state = initState, action) {
  const handler = ACTION_HANDLERS[action.type]
  return handler ? handler(state, action) : state
}



