import React, { Component } from 'react'
import Product from '../../../containers/ProductContainer'
import CSSModule from 'react-css-modules'

import Btn from './Btn'

@CSSModule(require('./LeftBar-new.scss'), { allowMultiple:true })
export default class LeftBar extends Component {
  state = {
    editMode: false,
    modeText: '管理'
  }

  toggleMode () {
    this.setState({
      editMode: !this.state.editMode,
      modeText: this.state.editMode ? '管理' : '完成'
    })
  }

  render() {
    return (
      <div styleName="root">
        <div styleName="list">
          <div styleName="item">
            <Btn
              label="已获取组件"
              icon="uncollected"
              {...this.state}
              toggleMode={this.toggleMode.bind(this)}
            />
            <div styleName="content">
              <Product editMode={this.state.editMode} />
            </div>
          </div>
        </div>

        <a styleName="more-btn" href="wandaffoap://jump/componentMarket">
          <i className="iconfont icon-puzzle" />
          <span>获取更多组件</span>
          <i styleName="right" className="iconfont icon-readMore" />
        </a>
      </div>
    )
  }
}



