import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import { updateStore } from './detail'
import _ from 'lodash'

function makeActionCreator (type, ...argNames) {
  return function (...args) {
    let action = { type }
    argNames.forEach((arg, index) => {
      action[ argNames[ index ] ] = args[ index ]
    })
    return action
  }
}

const ADD_ELEMENT = 'ADD_ELEMENT'
const SET_LAYOUT = 'SET_LAYOUT'

const SELECT_ELEMENT = 'SELECT_ELEMENT' // same as src/routes/Shop/routes/Editor/modules/detail.js
const SAVE_DETAIL = 'SAVE_DETAIL' // same as src/routes/Shop/routes/Editor/modules/detail.js
const CANCEL_ELEMENT = 'CANCEL_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js
const DELETE_ELEMENT = 'DELETE_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js'
const SAVE_ELEMENT = 'SAVE_ELEMENT' // To : src/routes/Shop/routes/Editor/modules/preview.js'
const REQUEST_PREVIEW = 'REQUEST_PREVIEW'

// export const addElement = makeActionCreator(ADD_ELEMENT, 'element', 'id')
export const addElement = makeActionCreator(ADD_ELEMENT, 'element')
export const setLayout = makeActionCreator(SET_LAYOUT, 'layouts')

const getPreviewData = data => ({
  type: REQUEST_PREVIEW,
  data
})

function getQrCode (dispatch, storeId) {
  const qrcodeUrl = getDomain('/client/v1/scanStore/create', { suffix: 'com' })

  fetchUtil.getJSON(qrcodeUrl, {
    size: 115,
    type: 'png',
    clientType: 4,
    storeId
  }).then(data => {
    dispatch(updateStore({ qrcode: data.homeUrl }))
    console.log(data.homeUrl)
  }).catch(e => {
    dispatch(updateStore({ qrcode: 'xxxx' }))
    console.log(e)
  })
}

export const fetchPreview = (pageID) => dispatch => {
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/page/${pageID}`)
  return fetchUtil.getJSON(apiUrl).then(data => {
    dispatch(getPreviewData(data))
    getQrCode(dispatch, data.storeId)
  })['catch'](e => {
    console.warn(e)
  })
}

export const selectElement = id => (dispatch, getState) => dispatch({
  type: SELECT_ELEMENT,
  id,
  selectedElement: _.cloneDeep(getState().preview.elements.find(e => e.id === id))
})

export const cancelElement = () => ({
  type: CANCEL_ELEMENT
})

export const saveElement = (element) => {
  console.log(element.codeSetting)
  return {
    type: SAVE_ELEMENT,
    codeSetting: _.cloneDeep(element.codeSetting),
    id: element.id
  }
}

export const actions = {
  addElement,
  setLayout,
  selectElement,
  cancelElement
}

const ACTION_HANDLERS = {

  [REQUEST_PREVIEW]: (state, action) => ({ ...state, ...action.data.viewDataDraft }),

  [ADD_ELEMENT]: (state, action) => ({
    ...state,
    elements: state.elements.map(element => ({
      ...element,
      selected: false
    })).concat({
      selected: true,
      ...action.element
    })
  }),

  [SET_LAYOUT]: (state, action) => ({
    ...state,
    ...action.layouts
  }),

  [CANCEL_ELEMENT]: state => {
    return {
      ...state,
      elements: state.elements.map(element => ({
        ...element, selected: false
      }))
    }
  },

  [DELETE_ELEMENT]: (state, action) => ({
    ...state,
    elements: state.elements.filter(element => element.id !== action.id)
  }),

  [SAVE_ELEMENT]: (state, action) => ({
    ...state,
    elements: state.elements.map(element => {
      if (element.id === action.id) {
        return {
          ...element,
          codeSetting: action.codeSetting
        }
      }
      return element
    })
  }),
  [SELECT_ELEMENT]: (state, action) => ({
    ...state,
    elements: state.elements.map(element => ({
      ...element,
      selected: element.id === action.id
    }))
  }),

  [SAVE_DETAIL]: (state, action) => {
    const elements = state.elements.map(e => {
      if (e.id === action.id) {
        return { ...e, detail: action.detail }
      } else {
        return e
      }
    })
    return { ...state, elements: elements }
  }

}

const defaultState = {
  elements: [],
  layouts: []
}

export default function productReducer (state = defaultState, action) {
  const handler = ACTION_HANDLERS[ action.type ]
  return handler ? handler(state, action) : state
}



