import React from 'react'
import PropTypes from 'prop-types'
import style from './Product-new.scss'
import CSSModule from 'react-css-modules'
import { Modal } from 'antd'
const confirm = Modal.confirm

const Product = (props) => {
  const { appId, appName, defaultLayout, appLogo, hideName,
    editMode, deleteProduct } = props

  return (
    <div styleName="component">
      { editMode && <span styleName="managerBtn"
          onClick={() => {
            confirm({
              title: `确定要删除 ${appName} 吗？`,
              content: '',
              onOk() {
                deleteProduct(appId)
                console.log('OK')
              },
              onCancel() {
                console.log('Cancel')
              },
            })
          }}>×</span> }
      <img
        height={50}
        src={appLogo || 'http://placeholder.qiniudn.com/100x100/#808080/fff'}
        alt={appName} />
      {hideName ? null : <span styleName="text"> {appName}</span>}
      { <span styleName="text">{defaultLayout.w}x{defaultLayout.h}</span> }
    </div>
  )
}

Product.propTypes = {
  appName: PropTypes.string.isRequired,
  width: PropTypes.number,
  height: PropTypes.number,
  defaultLayout: PropTypes.object.isRequired,
  appLogo: PropTypes.string.isRequired,
  hideName: PropTypes.bool
}

export default CSSModule(Product, style)



