import React, { Component } from 'react'
import PropTypes from 'prop-types'
import CSSModule from 'react-css-modules'
import formatter from 'utils/formatter'
import cn from 'classnames'
@CSSModule(require('./index-new.scss'), { allowMultiple:true })
export default class Btn extends Component {
  static propTypes={
    label: PropTypes.string,
    icon: PropTypes.string,
    extra: PropTypes.string,
    onClick: PropTypes.func,
    bottom: PropTypes.bool,
  };

  render() {
    const { icon, extra, bottom, modeText, toggleMode } = this.props
    let label = formatter.cut(this.props.label, 20)
    return (
      <div styleName={cn('root', { bottom })} >
        {icon && <i className={`iconfont icon-${icon}`} />}
        <span styleName="label">{label}</span>
        {extra && <i styleName="right" className={`iconfont icon-${extra}`} />}
        <span styleName="managerBtn" onClick={toggleMode}>{modeText}</span>
      </div>
    )
  }
}



