import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { findDOMNode } from 'react-dom'

import Preview from '../../containers/PreviewContainer'
import EditorDragLayer from '../EditorDragLayer'
import Detail from '../../containers/DetailContainer'
import LeftSideBar from './LeftBar'
import style from './MobileEditor-new.scss'
import CSSModule from 'react-css-modules'
import Slider from 'react-rangeslider'

export class Editor extends Component {
  constructor (props, context) {
    super(props, context)
    this.state = {
      show: false,
      value: 0
    }
  }

  handleChange = (value) => {
    // if(!this.isMounted()) { return }
    const scrollDom = findDOMNode(this.refs.scrollWrap)
    if (!scrollDom) {
      return
    }
    const dis = parseInt((scrollDom.scrollHeight - scrollDom.clientHeight) * value / 100)

    if (dis > 0 || scrollDom.scrollTop !== dis) {
      scrollDom.scrollTop = dis
      this.setState({
        value: value
      })
    } else if (scrollDom.scrollHeight - scrollDom.clientHeight <= 0) {
      scrollDom.scrollTop = 0
      this.setState({
        value: 100
      })
    }
  }

  scrollIntoView = () => {
    this.handleChange(100)
  }

  onLayoutChange = () => {
    const scrollDom = findDOMNode(this.refs.scrollWrap)
    if (!scrollDom) {
      return
    }
    setTimeout(() => {
      if (scrollDom) {
        const show = scrollDom.scrollHeight > scrollDom.clientHeight
        this.setState({
          show
        })
      }
    }, 500)
  }

  render () {
    const { value, show } = this.state
    const { pageID } = this.props.params

    return <div styleName="edit-shop-container">
      <LeftSideBar />
      <div styleName="editor-content">
        <div styleName="preview">
          <div styleName="bg-phone">
            <div styleName="mobile-header">
              <img className="clock"
                   src="http://timg.ffan.com/convert/resize/url_T1AOhTB7Wg1RCvBVdK/tfs/1.png"
              />
            </div>
            <div styleName="scroll-wrap" ref="scrollWrap">
              <Preview onLayoutChange={this.onLayoutChange} pageID={pageID}
                       scrollIntoView={this.scrollIntoView} />
            </div>
            <div styleName="slider-wrap">
              { show ? <Slider value={value} reverse orientation="vertical"
                               tooltip={false} onChange={this.handleChange} /> : ''}
            </div>
          </div>
        </div>
      </div>
      <div styleName="sidebar-right">
        <Detail pageID={pageID} />
      </div>
      <pre />
      <EditorDragLayer />
    </div>
  }
}

export default CSSModule(Editor, style, {
  allowMultiple: true
})

Editor.propTypes = {
  products: PropTypes.array,
  params: PropTypes.object
}



