import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Mask from '../Mask'
import './Modal.scss'
import DelayRenderer from '../Protal/DelayRenderer'
import Alert from './alert'
import Confirm from './confirm'

class Modal extends Component {
  static defaultProps = {
    type: 'alert',
    active: false,
    hideButtons: false,
    onClose: () => {},
    onOverlayClick: () => {}
  }

  getModalContent (type) {
    const { active, ...props } = this.props
    let content
    switch (type) {
      case 'alert':
        content = <Alert {...props} active={active}>
          {props.children}
        </Alert>
        break
      case 'confirm':
        content = <Confirm {...props} active={active}>
          {props.children}
        </Confirm>
        break
      default:
        content = null
    }
    return content
  }

  render () {
    const { active, type, ...props } = this.props
    return <Mask {...props} active={active} onClick={props.onCloseModal && props.onCloseModal}>
      {this.getModalContent(type)}
    </Mask>
  }
}

Modal.propTypes = {
  type: PropTypes.string.isRequired,
  active: PropTypes.bool,
  hideButtons: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  onOverlayClick: PropTypes.func
}
export default DelayRenderer()(Modal)



