import React, { Component } from 'react'
import PropTypes from 'prop-types'
import ReactGridLayout from 'react-grid-layout-fix-compact'
import cx from 'classnames'
import Element from '../../containers/ElementContainer'
import './Preview.scss'
// import CSSModule from 'react-css-modules'

export class Preview extends Component {
  componentDidMount () {
    this.props.fetchPreview(this.props.pageID)
  }

  static defaultProps = {
    gridProps: {
      className: 'preview-layout',
      cols: 4,
      rowHeight: 52.5,
      selectedCls: 'selected',
      width: 212,
      margin: [ 0, 0 ]
    }
  }

  onLayoutChange = (layouts) => {
    this.props.onLayoutChange()
    this.props.setLayout(layouts.map(l => ({
      i: l.i, w: l.w, h: l.h, x: l.x, y: l.y
    })))
  }

  onSelected = (id) => {
    this.props.selectElement(id)
  }

  generateLayout (e) {
    let { layouts } = this.props.preview
    try {
      const lay = layouts.find(l => l.i === e.id)
      return lay || { ...e.defaultLayout, ...{ x: 0, y: Infinity } }
    } catch (e) {
      return {}
    }
  }

  generateStyle (e) {
    if (e.moduleType !== 'html5') {
      return {
        backgroundSize: 'cover',
        backgroundImage: `url(${e.appPreviewImage})`
      }
    } else {
      return {}
    }
  }

  del = (id) => {
    const deleteElement = this.props.deleteElement
    if (window.confirm('确认删除这个组件吗？')) {
      deleteElement(id)
    }
  }

  generateDOM () {
    if (!this.props.preview) {
      return null
    }
    const elements = this.props.preview.elements

    return elements.map(ele => {
      const className = ele.selected ? 'selected' : ''
      const id = ele.id

      return <div key={id} className={className}
        style={this.generateStyle(ele)}
        onClick={(e) => {
          this.onSelected(id)
          e.stopPropagation()
        }}
        data-grid={this.generateLayout(ele)}>
        <Element {...ele} layout={this.generateLayout(ele)} gridProps={this.props.gridProps} />
        <span onClick={(e) => {
          this.del(id)
          e.stopPropagation()
        }}
          className={cx('delete-btn', {
            show: ele.selected
          })} >          &times;</span>
      </div>
    })
  }

  render () {
    const { connectDropTarget, gridProps } = this.props
    return connectDropTarget(
      <div className="shop-info" onClick={() => { this.props.cancelElement() }}>
        <ReactGridLayout {...gridProps}
          onLayoutChange={this.onLayoutChange}>
          {this.generateDOM()}
        </ReactGridLayout>
      </div>
    )
  }
}

Preview.propTypes = {
  pageID: PropTypes.string.isRequired,
  onLayoutChange: PropTypes.func,
  selectElement: PropTypes.func.isRequired,
  deleteElement: PropTypes.func.isRequired,
  canDrop: PropTypes.bool.isRequired,
  isOver: PropTypes.bool.isRequired,
  connectDropTarget: PropTypes.func.isRequired,
  setLayout: PropTypes.func.isRequired,
  cancelElement: PropTypes.func,
  gridProps: PropTypes.object,
  preview: PropTypes.object.isRequired,
  fetchPreview: PropTypes.func.isRequired
}

export default Preview



