import React, { Component } from 'react'
import CSSModule from 'react-css-modules'

@CSSModule(require('./index-new.scss'), { allowMultiple:true })
export default class TitleWithBtn extends Component {
  constructor(props) {
    super(props)
    let hasMore = false
    let showLen = props.showLen || 45
    if (props.content.length > showLen) {
      hasMore = true
    }
    this.state = {
      hasMore,
      showLen,
      fold: true,
      content: props.content
    }
  }

  showDetail() {
    const { fold, } = this.state

    this.setState({
      fold: !fold,
    })
  }

  render() {
    const { fold, showLen, content, hasMore } = this.state

    return (
      <div>
        <p styleName="cont">
          { fold ? content.slice(0, showLen - 1) + '...' : content }
        </p>
        { hasMore ? <span styleName="moreBtn" onClick={this.showDetail.bind(this)}>
          { fold ? '详情' : '折叠'}
        </span> : null}
      </div>
    )
  }
}



