const initialState = {
  products: []
}

export const getProduct = (data) => ({
  type: REQUEST_PRODUCTS,
  data
})

export const fetchProducts = () => {
  const apiUrl = getDomain('/app/v1/bo/v1/web/merchant/widgets')
  return (dispatch, getState) => fetchUtil.getJSON(apiUrl).then(products => {
    dispatch(getProduct({ products }))
  })['catch'](e => {
    console.warn(e)
  })
}

export const deleteProduct = (appId) => (dispatch, getState) => {
  const apiUrl = getDomain(`/app/v1/bo/v1/web/merchant/removeApp/${appId}`)
  return fetchUtil.getJSON(apiUrl, { appType: 2 }).then(products => {
    dispatch({
      type: DELETE_PRODUCT,
      appId
    })
  })['catch'](e => {
    console.warn(e)
    dispatch({
      type: DELETE_PRODUCT,
      appId
    })
  })
}

export const actions = {
  fetchProducts,
  deleteProduct
}

const ACTION_HANDLERS = {
  [REQUEST_PRODUCTS]: (state, action) => {
    return {
      ...state,
      ...action.data
    }
  },
  [DELETE_PRODUCT]: (state, action) => {
    return {
      ...state,
      products: state.products.filter(item => +item.appId !== +action.appId)
    }
  }
}

export default function productReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[ action.type ]
  return handler ? handler(state, action) : state
}



