import { connect } from 'react-redux'
import { compose } from 'redux'
import Detail from '../components/Detail'
import { saveDetail, savePage, publishPage,
  deleteElement, cancelElement, editElement } from '../modules/detail'
import { saveElement } from '../modules/preview'

const mapStateToProps = state => ({
  detail: state.detail
})

const mapDispatchToProps = {
  saveDetail,
  savePage,
  publishPage,
  saveElement,
  deleteElement,
  cancelElement,
  editElement
}

export default compose(
  connect(mapStateToProps, mapDispatchToProps)
)(Detail)



