import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Business from 'components/business'
import Modal from 'components/Modal'
import cx from 'classnames'
import style from './Detail-new.scss'
import CSSModule from 'react-css-modules'
import FullText from './FullText'

export class Detail extends Component {
  showMsg = (msg) => {
    this.setState({
      msgModal: {
        ...this.state.msgModal,
        visible: true,
        msg
      }
    })
    setTimeout(() => {
      this.setState({
        msgModal: {
          ...this.state.msgModal,
          visible: false
        }
      })
    }, 1500)
  }

  savePageHandler = () => {
    const { savePage, pageID } = this.props
    savePage(pageID).then(data => {
      this.showMsg('保存成功')
    }).catch(e => {
      this.showMsg(`保存失败|${e.status}|${e.msg}`)
    })
  }

  publishPageHandler = () => {
    const { publishPage, detail: { pagePublish }, pageID } = this.props
    if (pagePublish !== 'start') {
      publishPage(pageID)['catch'](e => {
        this.showMsg(`保存失败|${e.status}|${e.msg}`)
      })
    }
  }

  getWidgetContainer = () => {
    const { detail, saveDetail, editElement } = this.props
    const { element } = detail || {}
    const { codeSetting } = element
    if (!element.id) {
      return <div styleName="share">
        <div styleName="qr" >
          <img src={detail.qrcode} alt="门店二维码" />
        </div>
        <p styleName="qr-text" style={{ 'textAlign': 'center' }}>用蓝海APP扫描<br />即可预览店铺装修效果</p>
      </div>
    }

    if (element.moduleType === 'html5') {
      const { Detail } = Business[element.moduleName]
      if (Detail) {
        // 自定义组件
        return <Detail {...detail} onChange={saveDetail} />
      } else {
        // H5 中非自定义组件？
      }
    }

    const allowEditItemList = ['input']
    let { appDesc, appName } = element
    // appDesc = '说的说的水电费梵蒂冈电饭锅梵蒂冈说的说的水电费梵蒂冈电饭锅梵蒂冈说的说的水电费梵蒂冈电饭锅梵蒂冈说的说的水电费梵蒂冈电饭锅梵蒂冈';

    if (Array.isArray(codeSetting) && codeSetting.length > 0) {
      return (
        <div styleName="editor-container">
          <div styleName="cm-detail">
            <h3 styleName="title">{appName}</h3>
            <FullText content={appDesc} />
          </div>

          {codeSetting.map(({ label, id, type, enableEdit, value, desc, audioList }) => enableEdit &&
            allowEditItemList.findIndex(item => item === type) > -1
            ? <div key={id} styleName="input-wrap">
              <label styleName="label">{label}</label>
              <input type="text" value={value} onChange={
                (e) => editElement(id, e.target.value)
              } />
              <span styleName="desc">{desc}</span>
            </div> : <div styleName="radio-wrap" key={id}>
              <label styleName="label">{id}</label>
              <div>
                {Array.isArray(audioList) && audioList.length > 0 &&
                  audioList.map(({ audioKey, audioValue }) => (
                    <span key={audioKey}
                    onClick={(e) => editElement(id, audioValue)}
                    className={cx('item', { 'selected': audioValue === value })}
                    >{audioKey}</span>
                ))}
              </div>
            </div>)}
        </div>
      )
    }
  }

  state = {
    modalActive: true,
    msgModal: {
      visible: false,
      msg: '保存成功'
    },
    maxDetailContLen: 45
  }

  onSaveEdit = () => {
    this.props.saveElement(this.props.detail.element)
    this.props.cancelElement()
  }

  onClose = () => {
    this.setState({
      modalActive: false
    })
  }

  onOpen() {
    this.setState({
      modalActive: true
    })
  }

  onToggle() {
    this.setState({
      modalActive: !this.state.modalActive
    })
  }

  render() {
    const { msgModal } = this.state
    const { detail, cancelElement } = this.props
    const { pagePublish, element } = detail
    return <div id="detail-container">
      <Modal type={'alert'}
        active={pagePublish === 'start'}
        onClose={this.onClose}
        onCancel={this.onClose}
        modalCls="preview-modal"
        hideButtons>
        <p className="center">发布中，请稍候 </p>
        <div className="center" styleName="ball-beat">
          <div />
          <div />
          <div />
        </div>
      </Modal>
      <Modal type={'alert'}
        active={msgModal.visible}
        modalCls="preview-modal"
        hideButtons>
        <p className="center">{msgModal.msg || '保存成功'} </p>
      </Modal>
      {this.getWidgetContainer()}
      {
        element.appId && Array.isArray(element.codeSetting)
          ? <div styleName="widgets-btn-container">
            <button className="m-btn" styleName="btn" onClick={cancelElement}>
              <i className="iconfont icon-close" />
              <span>取消</span>
            </button>
            <button className="m-btn" styleName="btn active" onClick={this.onSaveEdit}>
              <i className="iconfont icon-save" />
              <span>保存</span>
            </button>
          </div> : <div styleName="widgets-btn-container">
            <button className="m-btn" styleName="btn" onClick={this.savePageHandler}>
              <i className="iconfont icon-save" />
              <span>保存</span>
            </button>
            <button className="m-btn" styleName="btn active" onClick={this.publishPageHandler}>
              <i className="iconfont icon-update" />
              <span>发布</span>
            </button>
          </div>
      }
    </div>
  }
}

Detail.defaultProps = {
  detail: {
    element: {}
  }
}

Detail.propTypes = {
  savePage: PropTypes.func.isRequired,
  publishPage: PropTypes.func.isRequired,
  saveDetail: PropTypes.func.isRequired,
  deleteElement: PropTypes.func.isRequired,
  cancelElement: PropTypes.func.isRequired,
  editElement: PropTypes.func.isRequired,
  detail: PropTypes.object.isRequired,
  saveElement: PropTypes.func.isRequired
}

export default CSSModule(Detail, style, {
  allowMultiple: true,
  // errorWhenNotFound: false
})



