import React, { Component } from 'react'
import PropTypes from 'prop-types'
import DragProduct from './DragProduct'
import style from './Product-new.scss'
import CSSModule from 'react-css-modules'
import Slider from 'react-rangeslider'
import { findDOMNode } from 'react-dom'
import device from 'utils/device'

export class ProductContainer extends Component {
  constructor (props, context) {
    super(props, context)
    this.state = {
      show: false,
      value: 0 /** Start value **/
    }
  }

  handleChange = (value) => {
    const scrollDom = findDOMNode(this.refs.scrollWrap)
    if (scrollDom.scrollHeight - scrollDom.clientHeight > 0) {
      this.setState({
        show: true
      })
    }
    const dis = (scrollDom.scrollHeight - scrollDom.clientHeight) * value / 100
    if (dis >= 0) {
      scrollDom.scrollTop = dis
      this.setState({
        value: value
      })
    }
  }

  componentDidMount () {
    this.props.fetchProducts().then(() => {
      if (device.isMobile()) {
        this.handleChange(0)
      }
    })
  }

  render () {
    console.log(this.props)

    const { product, ...rest } = this.props
    const { products = [], } = product

    const show = this.state.show

    return (
      <div styleName="gui-wrap">
        <div styleName="gui-container" ref="scrollWrap">
          <ul styleName="gui-list">
            {products
              .map((p, i) => <li styleName="items" key={i}>
                <DragProduct {...p} {...rest} />
              </li>)}
          </ul>
        </div>
        <div styleName="slider-wrap">
          { show ? <Slider value={this.state.value} orientation="vertical" reverse
            tooltip={false} onChange={this.handleChange} /> : null }
        </div>
      </div>

    )
  }
}

ProductContainer.defaultProps = {
  product: {
    products: []
  }
}

ProductContainer.propTypes = {
  product: PropTypes.object.isRequired,
  fetchProducts: PropTypes.func.isRequired
}

export default CSSModule(ProductContainer, style)



