import React from 'react'
import PropTypes from 'prop-types'
import './Modal.scss'
import classnames from 'classnames'

const Alert = ({ active, hideButtons, modalCls, ...props }) => {
  return <div className={classnames('modal', 'alert', { [`${modalCls}`]: modalCls }, { 'active': active })}>
    <div className={classnames('modalInner')}>
      <div className={'modalText'}>
        {props.title ? <h3 className="popup-title">
          <p className="icon-turnoff" onClick={props.onClose}>关闭</p>
          选择{props.text ? props.text : '应用'}</h3> : ''
        }
        {props.children}
      </div>
      {!hideButtons &&
      <div className={'modalButtons'}>
        <span className={'btn'} onClick={e => {
          props.onClose()
          props.onConfirm()
        }}>          确定</span>
      </div>
      }
    </div>
  </div>
}

Alert.propTypes = {
  hideButtons: PropTypes.bool,
  active: PropTypes.bool,
  modalCls: PropTypes.string,
  onClose: PropTypes.func.isRequired,
  onConfirm: PropTypes.func,
  text: PropTypes.string,
  title: PropTypes.string,
  children: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.array
  ])
}

Alert.defaultProps = {
  hideButtons: false,
  active: false,
  modalCls: '',
  onClose: () => {},
  onConfirm: () => {}
}

export default Alert



