/**
 * Created by yaoyi on 17/4/11.
 */
export default {
  cut:(str, len) => {
    if (typeof str !== 'string') return ''
    let realLen = 0
    for (var i = 0; i < str.length; i++) {
      if (realLen >= len) {
        return str.substring(0, i) + '...'
      }
      let ch = str.charCodeAt(i)
      if (ch >= 0 && ch <= 128) {
        realLen += 1
      } else {
        realLen += 2
      }
    }
    if (realLen >= len) {
      return str.substring(0, str.length) + '...'
    } else {
      return str
    }
  }
}



